import * as React from 'react';
import Masonry from 'react-masonry-component';
 
const masonryOptions = {
    transitionDuration: 0
};

import srcJWB from '../../../../assets/jasawebsitebanjarmasin.png'
 
const imagesLoadedOptions = { background: '.my-bg-image-el' }
 
class Gallery extends React.Component {
    render() {
        const listProjects = [
            { title: 'Website - KalselJS', type: 'Concept, Design, Development', srcImg: 'https://cdn.dribbble.com/users/1577045/screenshots/5068687/new_poster.png' },
            { title: 'Help Center - Kitabisa', type: 'Development', srcImg: 'https://picsum.photos/200/300/?random' },
            { title: 'Jasa Website Banjarmasin', type: 'Concept, Design, Development', srcImg: srcJWB },
            { title: 'Division Icon - Munaqadh', type: 'Design, Development', srcImg: 'https://cdn.dribbble.com/users/1577045/screenshots/4690653/division.png' },
        ]
        const childElements = listProjects.map(function(element){
           return (
                <li className="col--m4 col--s12 project-list--item">
                    <img src={element.srcImg} />
                    <p>
                        {element.title}
                        <small>{ element.type }</small>
                    </p>
                </li>
            );
        });
    
        return (
            <Masonry
                className={'project-list'} // default ''
                elementType={'ul'} // default 'div'
                options={masonryOptions} // default {}
                disableImagesLoaded={false} // default false
                updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
                imagesLoadedOptions={imagesLoadedOptions} // default {}
            >
                {childElements}
            </Masonry>
        );
    }
}
 
export default Gallery;