import React from 'react'

import styled from 'react-emotion'

const Wrapper = styled.div`
   width: 100%;
   margin: 2em 0;

   &:nth-child(even){
       text-align: right;
       padding-right: 55%;

       div {
            float: right;
       }
   }

   &:nth-child(odd){
       text-align: left;
       padding-left: 55%;

        div {
            float: left;
        }
   }
   
   @media screen and (max-width: 767px){
      
      &:last-child {
        margin-bottom: 0;
      }

      &:nth-child(even),
      &:nth-child(odd){
          padding: 0 1em;

          div {
              float: none;
              text-align: center;
          }
      }
   }
`

const Item  = styled.div `
   text-align: left;

   h2 {
       margin-bottom : 0;
   }

   p {
       font-weight: bold;
   }
   p small {
       font-weight: normal;
       display: block;
       font-size: 14px;
       line-height: 1.1em;
   }
`

const Project = { Wrapper, Item };

export default Project